.sidebar {
display: flex;
flex-direction: column;
  height: 50%;
  width: 0;
  position: fixed;
  top: 0;
  right: 0;
  background:none;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidebarOpen {
  width: 250px;
  z-index: 9999;
}

.sidebar a {
  margin: 0 15px;
  padding: 10px 0;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  transition: color 0.3s;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  font-weight: bold;
}

.sidebar a:hover {
  color: #fea6a6;
  cursor: pointer;
  transform: scale(1.05);
  transition: all 0.3s ease-in-out;
}
.btn {
  position: right;
  width: 150px;
  height: 50px;
  border-radius: 5px;
  border: none;
  transition: all 0.5s ease-in-out;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 600;
  display: flex;
  align-items: center;
  background: #040f16;
  color: #f5f5f5;
}

.btn:hover {
  box-shadow: 0 0 20px 0px #2e2e2e3a;
}

.btn .icon {
  position: absolute;
  height: 40px;
  width: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
}

.btn .text {
  transform: translateX(55px);
}

.btn:hover .icon {
  width: 175px;
}

.btn:hover .text {
  transition: all 0.5s;
  opacity: 0;
}

.btn:focus {
  outline: none;
}

.btn:active .icon {
  transform: scale(0.85);
}
@media (max-width: 768px) {
  .nav-item {
      padding: 10px;
      display: block;
  }

  .header {
      flex-direction: column;
      align-items: center;
  }
}